import http from '@/utils/request.js'

export const Log = {
    async  get(params) {
        return await http({
            url: '/api/log/list',
            params
        });
    },
    async  delete(data) {
        return await http({
            url: '/api/log/dels',
            method: 'delete',
            data
        });
    }
}

export const User = {
    async  login(data) {
        return await http({
            url: '/api/login',
            method: 'post',
            data
        });
    },
    async loginOut(){
        return await http({
            url:'/api/loginOut',
            method:'post'
        })
    }
}

export const Project = {
    async  get(params) {
        return await http({
            url: '/api/project/list',
            params
        });
    },
    async  delete(data) {
        return await http({
            url: '/api/project/dels',
            method: 'delete',
            data
        });
    }
}



