<template>
  <div class="fy-search-box">
      <div class="fy-search-left">
          <slot name="left"></slot>
      </div>
      <div class="fy-search-right">
          <slot name="right"></slot>
      </div>
  </div>
</template>

<script>
export default {
    name:'SearchBox'
}
</script>

<style lang="less">
    .fy-search-box{
        display: flex;
        flex-direction: row;
        .fy-search-left{
            flex: 1;
        }
        .fy-search-right{
            width: 200px;
            display: flex;
            justify-content: flex-end;
        }
    }
</style>



